<template>
  <form @submit.prevent="getNu">
    <input type="text" v-model="id">
    <button type="submit">
        Buscar
    </button>
  </form>

  <div class="row mt-5">

      <etiquetaNumero class="col"
      :numero = "getNum.numero"
      :ocupado = "getNum.ocupado"
      />

    <button class="col btn btn-primary"
        @click="seleccionarNumero"
    >
        Seleccionar
    </button>

</div>

</template>

<script>
import { defineAsyncComponent } from 'vue'
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
    data(){
        return{
            id:''
        }
    },
    components:{
        etiquetaNumero : defineAsyncComponent(()=> import('./etiquetaNumero.vue'))
    },
    computed:{
        ...mapGetters('numeros',['getNum']),
        ...mapGetters('cliente',['getClient'])
    },
    methods:{
        ...mapActions('numeros',['getNumero', 'updateNumero']),
        ...mapMutations('numeros',['setNumero']),
        getNu(){
            this.getNumero(this.id)
        },

        seleccionarNumero(){
            var numero = {
                id: this.getNum._id,
                ocupado: true,
                cliente: this.getClient._id
            }
            this.updateNumero(numero)
        }
    },
    unmounted(){
        this.setNumero({})
    }
}
</script>

